import { Component, OnInit, Output, EventEmitter } from "@angular/core";
import { Commfunc } from "../../../../../../@uisftech/common/service/commonFun";
import { ConfirmationService, Message } from "primeng/primeng";
import { CALENDAR_CN } from "../../../../../../@uisftech/common/constant/config";
import { ElementAdjustmentService } from "../../../http/element-adjustment.http.service";
import { CreditCardSavingsAdjustmentQueryAddBean } from "./bean/credit-card-savings-adjustment-query-add.bean";
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from "../../../../../../@uisftech/common/service/http.constant";
import { checkNumber, checkEnAndNum } from "../../../../../../@uisftech/common/service/validator";
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { BussinessMap } from "../bean/AppproResult.bean";
import * as _ from 'lodash';

@Component({
    selector: 'creditCardSavingsAdjustmentQueryAdd',
    templateUrl: './credit-card-savings-adjustment-query-add.component.html',
    styleUrls: ['./credit-card-savings-adjustment-query-add.component.scss'],
    providers: [Commfunc, ConfirmationService, ElementAdjustmentService]
})
export class CreditCardSavingsAdjustmentQueryAddComponent implements OnInit {
    @Output() outDisplay: EventEmitter<any> = new EventEmitter();
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    code: any;
    creditCardAdjustAddbean: CreditCardSavingsAdjustmentQueryAddBean = new CreditCardSavingsAdjustmentQueryAddBean();//单户
    msgs: Message[];
    ch: any = CALENDAR_CN;
    singleDisplay: boolean = true;//单一模式
    batchDisplay: boolean = false;//批量模式

    modificationMethodList: any = [];
    operationTypeOptions: any = [];//操作类型


    dataBatch: any;//批量入参
    UPLOAD: string = TZB_CUS_UPLOAD;
    DOWNLOAD: string = TZB_CUS_DOWNLOAD;
    uploadedFiles: any = [];//上传
    successNum: number = 0;//成功的条数
    failNum: number = 0;//失败的条数
    failInfoList: any = [];//批量数据
    createDate = new Date();//经办日期
    assistantUserIdList: any = [];//老协储员号
    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';
    //选人
    downloadPerson: boolean = false;
    waitSelUserList: any;
    outVal: any;
    extParam: any;

    //表单校验
    introform: FormGroup;//单户
    batchform: FormGroup;//批量
    newAssistantUserIdDot: string;//现协储员机构

    appSeqNo: string;//
    adjustType: string;//
    bussinessMap: BussinessMap = new BussinessMap();
    adjustUserIdBtach: string;//批量协储员号 老
    receiveUserIdBtach: string;//批量协储员号 新
    oldUserId: string;//入参
    newUserId: string;//入参
    flagShow: string;//入参标志
    uploadFlag: boolean = false;//是否已经上传，默认否
    blockDisplay: any = {
        flag: false,
    }
    oldOrgId: string = '';
    newOrgId: string = '';

    constructor(
        public fb: FormBuilder,
        public commfunc: Commfunc,
        public httpService: ElementAdjustmentService,
        public confirmationService: ConfirmationService,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
        if (this.user.extParam) {
            this.extParam = this.user.extParam;
        }
        this.introform = this.fb.group({
            'updateType': new FormControl('', Validators.required),
            'creditCardNo': new FormControl('', Validators.required),
            'acctName': new FormControl('', Validators.required),
            'assistantUserId': new FormControl('', Validators.required),
            'assistantUserName': new FormControl(''),
            'assistantUserIdDot': new FormControl('', Validators.required),
            'newAssistantUserId': new FormControl('', Validators.required),
            'newAssistantUserName': new FormControl('', Validators.required),
            'newAssistantUserIdDot': new FormControl('', Validators.required),
            'userName': new FormControl('', Validators.required),
            'orgName': new FormControl('', Validators.required),
            'createDate': new FormControl('', Validators.required),
        });
        this.batchform = this.fb.group({
            'updateType': new FormControl('', Validators.required),
            'userName': new FormControl('', Validators.required),
            'orgName': new FormControl('', Validators.required),
            'createDate': new FormControl('', Validators.required),
        });
    }
    ngOnInit() {
        this.codeValues();
        this.modificationMethodList = this.code['AdjustmentUpdateType'];//卡修改方式
        this.operationTypeOptions = this.code['operationTypeList'];//操作类型
        this.creditCardAdjustAddbean.updateType = '1';
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //单户 和 批量切换
    targetChange() {
        if (this.creditCardAdjustAddbean.updateType == "1") {
            this.singleDisplay = true;
            this.batchDisplay = false;
        }
        if (this.creditCardAdjustAddbean.updateType == "2") {
            this.singleDisplay = false;
            this.batchDisplay = true;
        }
    }
    //回显老协储员信息
    custShow() {

        if (checkNumber(null, this.creditCardAdjustAddbean.creditCardNo)) {
            this.doEmpty('card');
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '卡号只能为数字!' }];
            return
        }

        if (this.creditCardAdjustAddbean.creditCardNo && this.creditCardAdjustAddbean.creditCardNo != '') {
            let param = {
                creditCardNo: this.creditCardAdjustAddbean.creditCardNo
            }
            this.httpService.queryCeCardAssistantUserIdInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.assistantUserIdList = [];
                    for (let item of data.assistantUserId) {
                        this.assistantUserIdList.push({ label: item, value: item })
                    }
                    this.creditCardAdjustAddbean.assistantUserId = this.assistantUserIdList[0].value;
                    this.creditCardAdjustAddbean.assistantUserName = data.assistantUserName;
                    this.creditCardAdjustAddbean.assistantUserIdDot = data.assistantUserIdDot;
                    this.creditCardAdjustAddbean.acctName = data.acctName;
                } else {
                    this.doEmpty('card');
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.doEmpty('card');
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }

    // 清空卡信息
    doEmpty(type) {
        if (type == 'card') {
            this.assistantUserIdList = [];
            this.creditCardAdjustAddbean.assistantUserId = '';
            this.creditCardAdjustAddbean.assistantUserName = '';
            this.creditCardAdjustAddbean.assistantUserIdDot = '';
            this.creditCardAdjustAddbean.acctName = '';
        } else if (type == 'assistantUserId') {
            this.creditCardAdjustAddbean.newAssistantUserName = '';
            this.newAssistantUserIdDot = '';
            this.creditCardAdjustAddbean.newAssistantUserIdDot = '';
        }
    }

    //回显新协储员信息
    showName() {
        if (checkEnAndNum(null, this.creditCardAdjustAddbean.newAssistantUserId)) {
            this.doEmpty('assistantUserId');
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '现协储员号只能是数字和字母!' }];
            return
        }
        if (this.creditCardAdjustAddbean.newAssistantUserId && this.creditCardAdjustAddbean.newAssistantUserId != '') {
            let param = {
                assistantUserId: this.creditCardAdjustAddbean.newAssistantUserId,
            }
            this.httpService.queryUserIdOrAssistantUserId(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.creditCardAdjustAddbean.newAssistantUserName = data.userName;
                    this.newAssistantUserIdDot = data.orgName;
                    this.creditCardAdjustAddbean.newAssistantUserIdDot = data.orgId
                } else {
                    this.doEmpty('assistantUserId');
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.doEmpty('assistantUserId');
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }
    //文件导入
    myUploader(event) {
        this.blockDisplay.flag = true;
        this.uploadFlag = false;
        if (event.xhr.response) { //上传成功 后台返回的 文件路径 和文件名
            this.uploadedFiles = JSON.parse(event.xhr.response);
            if (this.uploadedFiles.length > 1) {
                this.blockDisplay.flag =false;
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '不支持上传多个文件！' }];
                return;
            }
            if (JSON.parse(event.xhr.response).success == false) {
                this.blockDisplay.flag = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: JSON.parse(event.xhr.response).errorMsg });
                return
            }
            let param = {
                adjustType: '03',
                filePath: this.uploadedFiles[0].fileUrl,
                fileName: this.uploadedFiles[0].fileName,
            }
            this.httpService.createBatchCeCardAdjustApply(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.blockDisplay.flag = false;
                    this.uploadFlag = true;
                    this.failInfoList = data.failInfoList;
                    this.successNum = data.successNum;
                    this.failNum = data.failNum;
                    this.dataBatch = data;
                    this.appSeqNo = data.appSeqNo;
                    this.adjustType = data.adjustType;
                    this.adjustUserIdBtach = data.adjustUserId;
                    this.receiveUserIdBtach = data.receiveUserId;
                    // 机构
                    this.oldOrgId = data.oldOrgId;
                    this.newOrgId = data.newOrgId;
                    this.outValue.emit({ appSeqNo: this.appSeqNo, adjustType: this.adjustType, flag: 0 })
                    this.msgs = [];
                    this.msgs = [{ severity: 'success', summary: '提示', detail: '文件导入成功!' }];
                } else {
                    this.blockDisplay.flag = false;
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.blockDisplay.flag = false;
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }
    submit() {
        if (this.creditCardAdjustAddbean.updateType == '1') {
            //校验的
            for (const i in this.introform.controls) {
                this.introform.controls[i].markAsDirty();
            }
            //处理是否提交
            for (const i in this.introform.controls) {
                if (!this.introform.controls[i].valid) {
                    return;
                }
            }
        } else if (this.creditCardAdjustAddbean.updateType == '2') {
            //校验的
            for (const i in this.batchform.controls) {
                this.batchform.controls[i].markAsDirty();
            }
            //处理是否提交
            for (const i in this.batchform.controls) {
                if (!this.batchform.controls[i].valid) {
                    return;
                }
            }
        }
        if (this.creditCardAdjustAddbean.updateType == '2') {
            if (!this.uploadFlag) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '请先上传文件!' }];
                return;
            }
            this.flagShow = '1';
            this.oldUserId = this.adjustUserIdBtach;
            this.newUserId = this.receiveUserIdBtach;
        } else {
            this.flagShow = '1';
            this.oldUserId = this.creditCardAdjustAddbean.assistantUserId;
            this.newUserId = this.creditCardAdjustAddbean.newAssistantUserId;
            this.oldOrgId = this.creditCardAdjustAddbean.assistantUserIdDot;
            this.newOrgId = this.creditCardAdjustAddbean.newAssistantUserIdDot;
        }
        let a = new Promise((resolve, reject) => {
            this.queryAssistantUserId(resolve);
        })
        //a全部响应成功后执行c
        let c = Promise.all([a]);
        c.then((res) => {
            this.comfirma(res[0]['bussinessMap']);
        })
    }
    comfirma(res_Map) {
        this.confirmationService.confirm({
            message: '确认要提交吗?',
            header: '提交',
            accept: () => {
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam['specifyNextNode']) {
                    delete s.extParam['specifyNextNode']
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.waitSelUserList = this.commfunc.query('XYK_XCYHTZ', res_Map);
                this.outVal = this.waitSelUserList.waitSelUserList;
                let chooseThis = this.waitSelUserList.display;
                let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                let a2 = commonHeader.extParam.specifyNextOpers;
                if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
                    this.downloadPerson = true;
                } else {
                    if (this.creditCardAdjustAddbean.updateType == '1') {
                        this.commit();
                    } else if (this.creditCardAdjustAddbean.updateType == '2') {
                        this.batchSub();
                    }
                }
            }
        });
    }
    //单户提交
    commit() {
        this.creditCardAdjustAddbean.operationType = '1';
        this.creditCardAdjustAddbean.agent = this.userId;
        this.creditCardAdjustAddbean.handleDate = this.commfunc.transDateN(this.createDate);
        this.creditCardAdjustAddbean.handleOrg = this.orgId;
        this.httpService.updateCeCardAssistantUserIdAdjustApply(this.creditCardAdjustAddbean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '提交成功!' }];
                this.outDisplay.emit(false)
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //批量提交
    batchSub() {
        if (this.successNum <= 0) {
            this.msgs = [];
            this.msgs = [{ severity: 'success', summary: '提示', detail: '当前没有成功的数据无法提交!' }];
            return;
        }
        this.httpService.submitBatchApply(this.dataBatch).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '提交成功!' }];
                this.outDisplay.emit(false)
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    outDisplayCall(event) {
        this.downloadPerson = event;
    }
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        let specifyNextNode = '';//节点处理名称
        this.outVal.forEach(item => {
            ss += item.tellerId + ',';
            specifyNextNode = item.outFlowId;
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        commonHeader.extParam['specifyNextNode'] = specifyNextNode;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        if (this.creditCardAdjustAddbean.updateType == '1') {
            this.commit();
        } else if (this.creditCardAdjustAddbean.updateType == '2') {
            this.batchSub();
        }
    }
    downloadHide() {
        this.downloadPerson = false;
    }
    //校验客户号只能输入数字
    onKeyupNumber(event, num) {
        if (event.target.value != '') {
            if (num == 1) {
                if (checkNumber(null, event.target.value)) {
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字!' }];
                    return
                }
            } else if (num == 2) {
                if (checkEnAndNum(null, event.target.value)) {
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字和字母!' }];
                    return
                }
            }
        }
    }
    //保存
    save() {
        //校验的
        for (const i in this.introform.controls) {
            this.introform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.introform.controls) {
            if (!this.introform.controls[i].valid) {
                return;
            }
        }
        this.creditCardAdjustAddbean['adjustType'] = '03';
        this.httpService.saveCeCardAdjustApp(this.creditCardAdjustAddbean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '保存成功!' }];
                this.outDisplay.emit(false)
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    //下载
    download() {
        open(TZB_CUS_DOWNLOAD + "?fileName=batchCreditCardAssistantUserId.xls&fileUrl=/copdata/moban", '_blank');
    }
    //批量的取消
    cancel() {
        if (this.appSeqNo && this.adjustType) {
            let arr = [];
            arr.push({
                appSeqNo: this.appSeqNo,
                adjustType: this.adjustType
            });
            let param = {
                infoList: arr
            };
            this.httpService.deleteCeCardAppInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.outValue.emit({ appSeqNo: this.appSeqNo, adjustType: this.adjustType, flag: 1 })
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                }
            });
        } else {
            this.outValue.emit({ appSeqNo: this.appSeqNo, adjustType: this.adjustType, flag: 2 })
        }
    }
    //查询原协储员与新协储员同属哪个部门
    queryAssistantUserId(resolve) {
        let param = {
            flag: this.flagShow,
            oldUserId: this.oldUserId,
            newUserId: this.newUserId,
            oldOrgId: this.oldOrgId,
            newOrgId: this.newOrgId,
        }
        this.httpService.queryAssistantUserIdAscription(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.bussinessMap = _.clone(data.resultMap);
                // this.bussinessMap['ascription'] = data.resultMap.ascription;
                // this.bussinessMap['adjustUserId'] = data.resultMap.adjustUserId;
                // this.bussinessMap['receiveUserId'] = data.resultMap.receiveUserId;
                // this.bussinessMap['userId'] = data.resultMap.userId;
                this.bussinessMap['userPost'] = this.extParam.postId;
                this.bussinessMap['userIdMap'] = {
                    userId: this.userId,
                    userPost: this.extParam.postId
                }
                resolve({ bussinessMap: this.bussinessMap });
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }
}