import { Component, OnInit, Input } from "@angular/core";
import { Commfunc } from "../../../../../../@uisftech/common/service/commonFun";
import { ConfirmationService, Message } from "primeng/primeng";
import { CreditCardSavingsAdjustmentQueryDetailsBean } from "./bean/credit-card-savings-adjustment-query-details.bean";

@Component({
    selector: 'creditCardSavingsAdjustmentQueryDetails',
    templateUrl: './credit-card-savings-adjustment-query-details.component.html',
    styleUrls: ['./credit-card-savings-adjustment-query-details.component.scss'],
    providers: [Commfunc, ConfirmationService]
})
export class CreditCardSavingsAdjustmentQueryDetailsComponent implements OnInit {
    @Input() inValue: any;
    msgs: Message[];
    creditDetailbean: CreditCardSavingsAdjustmentQueryDetailsBean = new CreditCardSavingsAdjustmentQueryDetailsBean();
    code: any;//
    modificationMethodList: any = [];//
    statusCardOptions: any = [];
    constructor(
        public commfunc: Commfunc,
    ) { }
    ngOnInit() {
        this.codeValues();
        this.modificationMethodList = this.code['AdjustmentUpdateType'];//卡修改方式
        this.statusCardOptions = this.code['statusCardAdjustment'];//状态
        console.log(this.inValue);
        this.creditDetailbean = this.inValue;
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
}