import { Component, OnInit, Input } from "@angular/core";
import { Commfunc } from "../../../../../../@uisftech/common/service/commonFun";
import { Message } from "primeng/primeng";
import { ElementAdjustmentService } from "../../../http/element-adjustment.http.service";
import { ActivatedRoute } from "@angular/router";
import { tableMessage } from "../../../../../../@uisftech/common/constant/config";
import { checkNumber, checkEnAndNum } from "app/@uisftech/common/service/validator";


@Component({
    selector: 'card-adjustment-query-approve',
    templateUrl: './card-adjustment-query-approve.component.html',
    styleUrls: ['./card-adjustment-query-approve.component.scss'],
    providers: [Commfunc, ElementAdjustmentService]
})
export class CardAdjustmentQueryApproveComponent implements OnInit {
    @Input() inValue: any;
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    msgs: Message[];
    code: any;
    first: number = 0;
    total: string;//总页数
    pageNum: number = 1;//当前页数
    pageSize: number = 10;//每页条数
    modificationMethodList: any = [];//卡修改方式
    operationTypeOptions: any = [];//操作类型
    statusCardOptions: any = [];//状态
    unionpayDisposeResultQuery: any = [];
    unionpayDisposeResultOptions: any = [];

    tableValueList: any = [];//批量数据表
    detailList: any = [];
    arrayList: {
        adjustType: "",
        adjustUserId: "",
        agent: "",
        ascription: "",
        assistantUserId: "",
        assistantUserIdDot: "",
        assistantUserName: "",
        creditCardNo: "",
        handleDate: "",
        handleOrg: "",
        newAssistantUserId: "",
        newAssistantUserIdDot: "",
        newAssistantUserName: "",
        receiveUserId: "",
        updateType: "",
        appSeqNo: "",
        acctName: "",
    }//审批传入的数据

    partPage: number;
    flag: string;//审批标志
    assistantUserIdList: any = [];//老协储员
    update_flag: boolean = true;//审批是否可修改
    newAssistantUserIdDot: string;//现协储员机构
    title: string = '修改';
    updateDisplay: boolean = false;
    inValueUpdate: any;//
    more_updateFlag: boolean = false;//批量可修改
    emitFlag: number = 0;//是否进行了修改
    constructor(
        public commfunc: Commfunc,
        public httpService: ElementAdjustmentService,
        public routeInfo: ActivatedRoute,
    ) { }
    ngOnInit() {
        console.log(this.routeInfo.snapshot.params);
        this.codeValues();
        this.modificationMethodList = this.code['AdjustmentUpdateType'];//卡修改方式
        this.operationTypeOptions = this.code['operationTypeList'];//操作类型
        this.statusCardOptions = this.code['statusCardAdjustment'];//状态
        this.unionpayDisposeResultQuery = this.code['UnionpayDisposeResultQuery'];//银联处理结果查询
        this.unionpayDisposeResultOptions = this.code['UnionpayDisposeResult'];//银联处理结果查询结果

        this.detailList = this.routeInfo.snapshot.params;
        this.arrayList = JSON.parse(this.routeInfo.snapshot.params['dataMap']);
        this.flag = this.routeInfo.snapshot.params['markFlag'];
        console.log(this.arrayList);
        if (this.routeInfo.snapshot.params['taskRate'] == '1' && this.flag != '2') {
            this.update_flag = false;
            this.more_updateFlag = true;
            this.emitFlag = 1;
        }
        this.newAssistantUserIdDot = this.arrayList.newAssistantUserIdDot;
        this.assistantUserIdList = [];
        this.assistantUserIdList.push({ label: this.arrayList.assistantUserId, value: this.arrayList.assistantUserId })
        this.arrayList.assistantUserId = this.assistantUserIdList[0].value;
        if (this.arrayList.updateType && this.arrayList.updateType == '1') {
            this.partPage = 1;
            this.query_new();
        } else {
            this.partPage = 2;
            this.query_new();
        }
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    // query() {
    //     this.arrayList['pageSize'] = this.pageSize;
    //     this.arrayList['pageNum'] = this.pageNum;
    //     this.httpService.queryCeCardAssistantUserIdAdjustApply(this.arrayList).subscribe(data => {
    //         if (data.returnCode.code == 'success') {
    //             this.tableValueList = data.infoList;
    //             this.total = data.totalNum;
    //         } else {
    //             this.msgs = [];
    //             this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
    //         }
    //     }, err => {
    //         this.msgs = [];
    //         this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
    //     });
    // }
    paginate(event) {
        //每页显示的条数
        this.pageSize = event.rows * 1;
        //当前页
        this.pageNum = event.page + 1;
        this.first = event.page * this.pageSize;
        this.query_new();
    }
    //回显老协储员信息
    custShow() {
        if (this.arrayList.creditCardNo && this.arrayList.creditCardNo != '') {
            let param = {
                creditCardNo: this.arrayList.creditCardNo
            }
            this.httpService.queryCeCardAssistantUserIdInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.assistantUserIdList = [];
                    for (let item of data.assistantUserId) {
                        this.assistantUserIdList.push({ label: item, value: item })
                    }
                    this.arrayList.assistantUserId = this.assistantUserIdList[0].value;
                    this.arrayList.assistantUserName = data.assistantUserName;
                    this.arrayList.assistantUserIdDot = data.assistantUserIdDot;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }
    //回显新协储员信息
    showName() {

        if (checkEnAndNum(null, this.arrayList.newAssistantUserId)) {
            this.doEmpty('assistantUserId');
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '现协储员号只能是数字和字母!' }];
            return
        }
        if (this.arrayList.newAssistantUserId && this.arrayList.newAssistantUserId != '') {
            let param = {
                assistantUserId: this.arrayList.newAssistantUserId,
            }
            this.httpService.queryUserIdOrAssistantUserId(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.arrayList.newAssistantUserName = data.userName;
                    this.arrayList.newAssistantUserIdDot = data.orgId;
                    this.newAssistantUserIdDot = data.orgName;
                } else {
                    this.doEmpty('assistantUserId');
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.doEmpty('assistantUserId');
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }

    // 清空卡信息
    doEmpty(type) {
        if (type == 'card') {
           
        } else if (type == 'assistantUserId') {
            this.arrayList.newAssistantUserName = '';
            this.arrayList.newAssistantUserIdDot = '';
            this.newAssistantUserIdDot = '';
        }
    }

    //校验客户号只能输入数字
    onKeyupNumber(event, num) {
        if (event.target.value != '') {
            if (num == 1) {
                if (checkNumber(null, event.target.value)) {
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字!' }];
                    return
                }
            } else if (num == 2) {
                if (checkEnAndNum(null, event.target.value)) {
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字和字母!' }];
                    return
                }
            }
        } else {
            if (num == 1) {
                this.assistantUserIdList = [];
                this.arrayList.assistantUserId = '';
                this.arrayList.assistantUserName = '';
                this.arrayList.assistantUserIdDot = '';
            } else if (num == 2) {
                this.arrayList.newAssistantUserName = '';
                this.arrayList.newAssistantUserIdDot = '';
                this.newAssistantUserIdDot = '';
            }
        }
    }
    //修改
    toEdit(col) {
        this.updateDisplay = true;
        this.inValueUpdate = col;
    }
    //修改回调
    updateCall(event) {
        if (event == false) {
            this.updateDisplay = false;
            this.query_new();
        }
    }

    // 删除
    toDelete(col) {
        if (this.tableValueList.length <= 1) {
            this.msgs = [];
            this.msgs = [{ severity: 'info', summary: '提示', detail: '至少保留一条数据' }];
            return
        }
        let arr = [];
        arr.push(
            // 流水号 + 卡号 + 调整类型
            { appSeqNo: col.appSeqNo, creditCardNo: col.creditCardNo, adjustType: '03' }
        )
        let param = {
            infoList: arr
        }
        this.httpService.deleteCeCardAppInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                // 重新查询
                this.query_new();
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }

    //新的查询，单户、批量
    query_new() {
        let params;
        if (this.arrayList.updateType && this.arrayList.updateType == '1') {
            //单户
            params = {
                appSeqNo: this.arrayList.appSeqNo,
                creditCardNo: this.arrayList.creditCardNo,
                pageNum: this.pageNum,
                pageSize: this.pageSize
            }
        } else {
            //批量
            params = {
                appSeqNo: this.arrayList.appSeqNo,
                pageNum: this.pageNum,
                pageSize: this.pageSize
            }
        }
        this.httpService.queryCeCardAdjustApp(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.infoList && data.infoList.length > 0) {
                    if (this.arrayList.updateType && this.arrayList.updateType == '1') {
                        this.arrayList = data.infoList[0];
                        this.arrayList['emitFlag'] = this.emitFlag;
                    } else {
                        this.tableValueList = data.infoList;
                        this.arrayList.assistantUserId = data.infoList[0].assistantUserId;
                        this.arrayList.newAssistantUserId = data.infoList[0].newAssistantUserId;
                        this.arrayList['assistantUserIdDot'] = data.infoList[0].assistantUserIdDot;
                        this.arrayList['newAssistantUserIdDot'] = data.infoList[0].newAssistantUserIdDot;
                        this.arrayList['emitFlag'] = this.emitFlag;
                    }
                }
                this.total = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
}