import { Component, OnInit, Output, Input, EventEmitter } from "@angular/core";
import { Commfunc } from "../../../../../../@uisftech/common/service/commonFun";
import { ConfirmationService, Message } from "primeng/primeng";
import { CreditCardSavingsAdjustmentQueryUpdataBean } from "./bean/credit-card-savings-adjustment-query-updata.bean";
import { CALENDAR_CN } from "../../../../../../@uisftech/common/constant/config";
import { ElementAdjustmentService } from "../../../http/element-adjustment.http.service";
import { checkNumber, checkEnAndNum } from "../../../../../../@uisftech/common/service/validator";
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { AppproResultBean, SubApproveMapBean, BussinessMap } from "../bean/AppproResult.bean";
import { CustomeHttpService } from "app/@uisftech/view/http/uisftech-view.http.service";
import * as _ from 'lodash';
@Component({
    selector: 'creditCardSavingsAdjustmentQueryUpdata',
    templateUrl: './credit-card-savings-adjustment-query-updata.component.html',
    styleUrls: ['./credit-card-savings-adjustment-query-updata.component.scss'],
    providers: [Commfunc, ConfirmationService, ElementAdjustmentService, CustomeHttpService]
})
export class CreditCardSavingsAdjustmentQueryUpdataComponent implements OnInit {
    @Input() inValue: any;
    @Output() outDisplay: EventEmitter<any> = new EventEmitter();
    creditCardAdjustUpdateBean: CreditCardSavingsAdjustmentQueryUpdataBean = new CreditCardSavingsAdjustmentQueryUpdataBean();
    msgs: Message[];
    ch: any = CALENDAR_CN;
    modificationMethodPromptDispaly: boolean = false;//修改方式 必选提示 开关

    code: any;
    modificationMethodList: any = [];
    operationTypeOptions: any = [];//操作类型
    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';
    createDate = new Date();//经办日期
    //选人
    downloadPerson: boolean = false;
    waitSelUserList: any;
    outVal: any;
    extParam: any;
    //表单校验
    introform: FormGroup;
    newAssistantUserIdDot: string;//现协储员机构

    //新的提交
    appproResultBean: AppproResultBean = new AppproResultBean();
    subApproveMapBean: SubApproveMapBean = new SubApproveMapBean();
    bussinessMap: BussinessMap = new BussinessMap();
    examineDisplay: any;
    extableData: any;
    checkValueNode: string;  //列表选项
    checkValuePeople: string;
    chosseNodeIndex: string;
    checkValuePeopleSign: any = [];
    mm: any = [];
    constructor(
        public fb: FormBuilder,
        public commfunc: Commfunc,
        public httpService: ElementAdjustmentService,
        public confirmationService: ConfirmationService,
        public customeHttpService: CustomeHttpService,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
        if (this.user.extParam) {
            this.extParam = this.user.extParam;
        }
        this.introform = this.fb.group({
            'updateType': new FormControl('', Validators.required),
            'creditCardNo': new FormControl('', Validators.required),
            'acctName': new FormControl('', Validators.required),
            'assistantUserId': new FormControl('', Validators.required),
            'assistantUserName': new FormControl(''),
            'assistantUserIdDot': new FormControl('', Validators.required),
            'newAssistantUserId': new FormControl('', Validators.required),
            'newAssistantUserName': new FormControl('', Validators.required),
            'newAssistantUserIdDot': new FormControl('', Validators.required),
            'agentName': new FormControl('', Validators.required),
            'handleOrgName': new FormControl('', Validators.required),
            'handleDate': new FormControl('', Validators.required),
            'appStatus': new FormControl('')
        });
    }
    ngOnInit() {
        this.codeValues();
        this.modificationMethodList = this.code['AdjustmentUpdateType'];//卡修改方式
        this.operationTypeOptions = this.code['operationTypeList'];//操作类型
        let newList = JSON.parse(JSON.stringify(this.inValue));
        this.creditCardAdjustUpdateBean = newList;
        this.newAssistantUserIdDot = newList.newAssistantUserIdDot;
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //回显老协储员信息
    custShow() {
        if (this.creditCardAdjustUpdateBean.creditCardNo && this.creditCardAdjustUpdateBean.creditCardNo != '') {
            let param = {
                creditCardNo: this.creditCardAdjustUpdateBean.creditCardNo
            }
            this.httpService.queryCeCardAssistantUserIdInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.creditCardAdjustUpdateBean.assistantUserName = data.assistantUserName;
                    this.creditCardAdjustUpdateBean.assistantUserIdDot = data.assistantUserIdDot;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }
    //回显新协储员信息
    showName() {
        if (checkEnAndNum(null, this.creditCardAdjustUpdateBean.newAssistantUserId)) {
            this.doEmpty('assistantUserId');
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '现协储员号只能是数字和字母!' }];
            return
        }
        if (this.creditCardAdjustUpdateBean.newAssistantUserId && this.creditCardAdjustUpdateBean.newAssistantUserId != '') {
            let param = {
                assistantUserId: this.creditCardAdjustUpdateBean.newAssistantUserId,
            }
            this.httpService.queryUserIdOrAssistantUserId(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.creditCardAdjustUpdateBean.newAssistantUserName = data.userName;
                    this.newAssistantUserIdDot = data.orgName;
                    this.creditCardAdjustUpdateBean.newAssistantUserIdDot = data.orgId
                } else {
                    this.doEmpty('assistantUserId');
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.doEmpty('assistantUserId');
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }

    // 清空卡信息
    doEmpty(type) {
        if (type == 'card') {

        } else if (type == 'assistantUserId') {
            this.creditCardAdjustUpdateBean.newAssistantUserName = '';
            this.newAssistantUserIdDot = '';
            this.creditCardAdjustUpdateBean.newAssistantUserIdDot = '';
        }
    }

    submit() {
        //校验的
        for (const i in this.introform.controls) {
            this.introform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.introform.controls) {
            if (!this.introform.controls[i].valid) {
                return;
            }
        }
        let a = new Promise((resolve, reject) => {
            this.queryAssistantUserId(resolve);
        })
        //a全部响应成功后执行c
        let c = Promise.all([a]);
        c.then((res) => {
            this.comfirma(res[0]['bussinessMap'])
        })
    }
    comfirma(res_Map) {
        this.confirmationService.confirm({
            message: '确认要提交吗?',
            header: '提交',
            accept: () => {
                //当前为追回打回时
                if (this.userId == this.creditCardAdjustUpdateBean.agent
                    && this.userId == this.creditCardAdjustUpdateBean.currTaskActors
                    && (this.creditCardAdjustUpdateBean.appStatus == '991' || this.creditCardAdjustUpdateBean.appStatus == '994')) {
                    this.backCommit();
                } else {
                    let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    if (s.extParam.specifyNextOpers) {
                        delete s.extParam.specifyNextOpers
                        let commonHeader = s;
                        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                    }
                    if (s.extParam['specifyNextNode']) {
                        delete s.extParam['specifyNextNode']
                        let commonHeader = s;
                        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                    }
                    this.waitSelUserList = this.commfunc.query('XYK_XCYHTZ', res_Map);
                    this.outVal = this.waitSelUserList.waitSelUserList;
                    let chooseThis = this.waitSelUserList.display;
                    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    let a2 = commonHeader.extParam.specifyNextOpers;
                    if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
                        this.downloadPerson = true;
                    } else {
                        this.commit();
                    }
                }
            }
        });
    }
    //提交
    commit() {
        this.creditCardAdjustUpdateBean.agentName = this.userName;
        this.creditCardAdjustUpdateBean.agent = this.userId;
        this.creditCardAdjustUpdateBean.handleDate = this.commfunc.transDateN(this.createDate);
        this.creditCardAdjustUpdateBean.handleOrg = this.orgId;
        this.creditCardAdjustUpdateBean.handleOrgName = this.orgName;
        this.creditCardAdjustUpdateBean.operationType = '2';
        this.httpService.updateCeCardAssistantUserIdAdjustApply(this.creditCardAdjustUpdateBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '提交成功!' }];
                this.outDisplay.emit(false)
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    outDisplayCall(event) {
        this.downloadPerson = event;
    }
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        let specifyNextNode = '';//节点处理名称
        this.outVal.forEach(item => {
            ss += item.tellerId + ',';
            specifyNextNode = item.outFlowId;
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        commonHeader.extParam['specifyNextNode'] = specifyNextNode;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        this.commit();
    }
    downloadHide() {
        this.downloadPerson = false;
    }
    //校验客户号只能输入数字
    onKeyupNumber(event) {
        if (event.target.value != '') {
            if (checkEnAndNum(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字和字母!' }];
                return
            }
        }
    }
    //追回打回的选人
    agree(param_) {
        this.bussinessMap.approve = '0';
        this.bussinessMap['dataMap'] = param_;
        this.subApproveMapBean.result = '0';
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        let param = {
            userIdMap: { userId: this.userId, userPost: this.extParam.postId },
            proxessDesc: '选人策略',
            taskId: param_.taskId,
            bussinessMap: this.bussinessMap,
            approveMap: this.subApproveMapBean,
        }
        this.customeHttpService.bpmFindNextNodeInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.result.nextOutData && data.result.nextOutData.length >= 1) {
                    let waitSelectUserList = data.result.nextOutData;
                    this.extableData = [];
                    this.extableData = waitSelectUserList;
                    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    let a = commonHeader.extParam.specifyNextOpers;
                    if (a == undefined || a == '' || a == null) {
                        if (this.extableData && this.extableData.length >= 1) {
                            if (this.extableData.length == 1 &&
                                ((this.extableData[0].selectUserList && this.extableData[0].selectUserList.length == 0) ||
                                    (this.extableData[0].selectUserList == undefined))) {
                                this.examineDisplay = false;
                                this.disposeSchedule(param_);
                            } else {
                                if (this.extableData.length == 1) {
                                    this.checkValueNode = this.extableData[0].outFlowId;
                                    if (this.extableData[0].nodeType == 'sign') {
                                        this.mm = [];
                                        if (this.extableData[0].selectUserList && this.extableData[0].selectUserList.length > 0) {
                                            for (let i = 0; i < this.extableData[0].selectUserList.length; i++) {
                                                this.mm.push(this.extableData[0].selectUserList[i].userId)
                                            }
                                        }
                                        this.checkValuePeopleSign = _.clone(this.mm);
                                    } else {
                                        this.checkValuePeopleSign = [];
                                    }
                                }
                                this.examineDisplay = true;
                            }
                        }

                    } else {
                        this.disposeSchedule(param_);
                    }
                } else {
                    this.disposeSchedule(param_);
                }
            }
            else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
    }
    disposeSchedule(param_) {
        this.bussinessMap.specifyNextNode = this.checkValueNode;
        if (this.checkValuePeople) {
            let sIndex = '';
            this.extableData.forEach((element, index) => {
                if (this.checkValueNode == element.outFlowId) {
                    sIndex = index + '';
                }
            });
            if (sIndex.length > 0) {
                let num = this.checkValuePeople.length - sIndex.length;
                this.bussinessMap.specifyNextOpers = this.checkValuePeople.substring(0, num) + ',';
            } else {
                this.bussinessMap.specifyNextOpers = this.checkValuePeople + ',';
            }
        }
        this.appproResultBean.bussinessMap = this.bussinessMap;
        this.appproResultBean.userId = this.userId;
        this.appproResultBean.approveMap = this.subApproveMapBean;
        this.appproResultBean.taskId = param_.taskId;
        this.customeHttpService.bpmNextTask(this.appproResultBean).subscribe(data => {
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (data.returnCode.code == 'success') {
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.msgs = [{ severity: 'success', summary: '提示', detail: '提交成功!' }];
                this.outDisplay.emit(false)
            } else {
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (s.extParam.specifyNextOpers) {
                delete s.extParam.specifyNextOpers
                let commonHeader = s;
                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
            }
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }
    doSubmit() {

        if (this.checkValueNode == '' || this.checkValueNode == undefined) {
            this.msgs = [];
            this.msgs.push({ severity: "error", summary: "提示", detail: "请选择下一处理节点" })
            return
        }

        let ss = '';
        // 获取选中节点类型
        this.extableData.forEach((element, index) => {
            if (this.checkValueNode == element.outFlowId) {
                ss = element.nodeType
            }
        });

        if (this.checkValuePeople == '' || this.checkValuePeople == undefined) {
            // 如果是会签节点和自动节点
            if (ss == 'sign' || ss == 'auto' || ss == 'end') {

            } else {
                this.msgs = [];
                this.msgs.push({ severity: "error", summary: "提示", detail: "请选择下一节点处理人" })
                return
            }
        }
        this.examineDisplay = false;
        this.disposeSchedule(this.creditCardAdjustUpdateBean);
    }
    //追回打回的关闭弹窗
    closeBack() {
        this.examineDisplay = false;
        this.extableData = [];
        this.checkValueNode = undefined;
        this.checkValuePeople = undefined;
    }
    choosePeople(col, ri) {
        // 清空会签信息
        this.checkValuePeopleSign = [];
        // 选中当前节点
        this.checkValueNode = col.outFlowId;
    }
    // 选择节点
    chooseNode(param, rowIndexNode) {
        if (param.outFlowId != this.checkValueNode) {
            this.checkValuePeople = undefined;
        }

        if (param.nodeType == 'sign') {
            this.mm = [];
            if (param.selectUserList && param.selectUserList.length > 0) {
                for (let i = 0; i < param.selectUserList.length; i++) {
                    this.mm.push(param.selectUserList[i].userId)
                }
            }
            this.checkValuePeopleSign = _.clone(this.mm);
        } else {
            this.checkValuePeopleSign = [];
        }
        this.chosseNodeIndex = rowIndexNode;
    }
    //打回的提交
    backCommit() {
        this.creditCardAdjustUpdateBean['adjustType'] = '03';
        this.httpService.updateCeCardAdjustApp(this.creditCardAdjustUpdateBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.queryTaskId(this.creditCardAdjustUpdateBean);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //查询打回追回状态的taskid
    queryTaskId(items) {
        let paramItem = {
            processId: items.standby2
        }
        this.httpService.bpmQueryUnDoTaskList(paramItem).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.pageData.result && data.pageData.result.length > 0) {
                    items['taskId'] = data.pageData.result[0].taskId;
                    this.agree(items);
                }
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }
    //查询原协储员与新协储员同属哪个部门
    queryAssistantUserId(resolve) {
        let param = {
            oldUserId: this.creditCardAdjustUpdateBean.assistantUserId,
            newUserId: this.creditCardAdjustUpdateBean.newAssistantUserId,
            oldOrgId: this.creditCardAdjustUpdateBean.assistantUserIdDot,
            newOrgId: this.creditCardAdjustUpdateBean.newAssistantUserIdDot,
        }
        this.httpService.queryAssistantUserIdAscription(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.bussinessMap = _.clone(data.resultMap);
                // this.bussinessMap['ascription'] = data.resultMap.ascription;
                // this.bussinessMap['adjustUserId'] = data.resultMap.adjustUserId;
                // this.bussinessMap['receiveUserId'] = data.resultMap.receiveUserId;
                // this.bussinessMap['userId'] = data.resultMap.userId;
                this.bussinessMap['userPost'] = this.extParam.postId;
                this.bussinessMap['userIdMap'] = {
                    userId: this.userId,
                    userPost: this.extParam.postId
                }
                resolve({ bussinessMap: this.bussinessMap });
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }
}