import { Routes, RouterModule } from "@angular/router";
import { CreditCardSavingsAdjustmentQueryComponent } from "./credit-card-savings-adjustment-query/credit-card-savings-adjustment-query.component";
import { ModuleWithProviders } from "@angular/core";
import { CardAdjustmentQueryApproveComponent } from "./credit-card-savings-adjustment-query/card-adjustment-query-approve/card-adjustment-query-approve.component";
export const routes: Routes = [
    {
        path: '',
        children: [
            {
                path: 'credit-card-savings-adjustment-query',
                component: CreditCardSavingsAdjustmentQueryComponent
            },
            {
                path: 'card-adjustment-query-approve',
                component: CardAdjustmentQueryApproveComponent
            }
        ]
    }

]

export const CreditCardSavingsAdjustmentRouting: ModuleWithProviders = RouterModule.forChild(routes);