import { Component, OnInit } from "@angular/core";
import { Commfunc } from "../../../../../@uisftech/common/service/commonFun";
import { ConfirmationService, Message } from "primeng/primeng";
import { CreditCardSavingsAdjustmentQueryBean } from "./bean/credit-card-savings-adjustment-query.bean";
import { ElementAdjustmentService } from "../../http/element-adjustment.http.service";
import { CreditCardBean } from "../../../custom/custom-information/mine-custom/integral-info-query/bean/creditCardDetail.bean";
import { tableMessage } from "../../../../../@uisftech/common/constant/config";
import { checkNumber, checkEnAndNum } from "../../../../../@uisftech/common/service/validator";
import { AppproResultBean, SubApproveMapBean, BussinessMap } from "./bean/AppproResult.bean";
import { CustomeHttpService } from "app/@uisftech/view/http/uisftech-view.http.service";
import * as _ from 'lodash';
import { CALENDAR_CN } from "../../../../../@uisftech/common/constant/config";
import { TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";

@Component({
    selector: 'creditCardSavingsAdjustmentQuery',
    templateUrl: './credit-card-savings-adjustment-query.component.html',
    styleUrls: ['./credit-card-savings-adjustment-query.component.scss'],
    providers: [Commfunc, ConfirmationService, ElementAdjustmentService, CustomeHttpService]
})
export class CreditCardSavingsAdjustmentQueryComponent implements OnInit {
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    code: any;
    modificationMethodList: any = [];
    statusCardOptions: any = [];//
    unionpayDisposeResultQuery: any = [];
    unionpayDisposeResultOptions: any = [];

    creditCardQrybean: CreditCardSavingsAdjustmentQueryBean = new CreditCardSavingsAdjustmentQueryBean();
    msgs: Message[];
    title: string;//弹出窗标题
    display: boolean = false;//弹出框
    total: string;//总页数
    first: number = 0;
    pageNum: number;//当前页数
    pageSize: number;//每页条数
    tableValueList: any = [];
    dispalyShow: string;
    assistantUserIdList: any = [];//老协储员列表

    inValueDetail: any;//详情数据
    inValueUpdate: any;//修改数据
    this_flag: boolean;//可操作的标志
    user: any;
    userId: string;

    //选人
    downloadPerson: boolean = false;
    waitSelUserList: any;
    outVal: any;
    extParam: any;
    params: any;//提交储存的数据

    valueAppSeqNo: any;//新增回调传过来的参数
    isCommitDis: boolean = true;
    //新的提交
    appproResultBean: AppproResultBean = new AppproResultBean();
    subApproveMapBean: SubApproveMapBean = new SubApproveMapBean();
    bussinessMap: BussinessMap = new BussinessMap();
    examineDisplay: any;
    extableData: any;
    checkValueNode: string;  //列表选项
    checkValuePeople: string;
    chosseNodeIndex: string;
    checkValuePeopleSign: any = [];
    mm: any = [];
    deleteFlag: any = [];
    deleteParam: any;

    userInValue: any;
    personPage: boolean = false;
    orgTreeDisplay: boolean = false;
    agentName: string;
    ownerOrgInValue: any;
    handleOrgName: string;
    ch: any = CALENDAR_CN;
    startDate: Date = null;
    endDate: Date = null;

    constructor(
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
        public httpService: ElementAdjustmentService,
        public customeHttpService: CustomeHttpService,
        public cusOperationHttpService: CusOperationHttpService,
    ) {

    }
    ngOnInit() {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user.extParam) {
            this.extParam = this.user.extParam;
        }
        this.codeValues();
        this.modificationMethodList = this.code['AdjustmentUpdateType'];//卡修改方式
        this.statusCardOptions = this.code['statusCardAdjustment'];//状态
        this.unionpayDisposeResultQuery = this.code['UnionpayDisposeResultQuery'];//银联处理结果查询
        this.unionpayDisposeResultOptions = this.code['UnionpayDisposeResult'];//银联处理结果查询结果

        this.creditCardQrybean.pageNum = 1;
        this.creditCardQrybean.pageSize = 10;

    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //新增
    add() {
        this.title = "新增";
        this.display = true;
        this.dispalyShow = '1';
    }
    //新增回调
    addBack(event) {
        this.display = false;
        this.queryFirst();
        this.msgs = [];
        this.msgs = [{ severity: 'success', summary: '提示', detail: '新增成功!' }];
    }
    //详情
    toDetails(col) {
        this.inValueDetail = col;
        this.title = "详情";
        this.display = true;
        this.dispalyShow = '3';
    }
    // 编辑
    toEdit(col) {
        if (col.updateType == '2') {
            this.msgs = [{ severity: 'success', summary: '提示', detail: '批量不可修改!' }];
            return;
        }
        this.inValueUpdate = col;
        this.title = "编辑";
        this.display = true;
        this.dispalyShow = '2';

    }
    //修改回调
    callBackUpdate(event) {
        this.display = false;
        this.queryFirst();
        this.msgs = [];
        this.msgs = [{ severity: 'success', summary: '提示', detail: '修改成功!' }];
    }

    //删除
    toDelete(item) {
        this.deleteFlag = [];
        this.deleteParam = JSON.parse(JSON.stringify(item));
        this.confirmationService.confirm({
            message: '确认要删除?',
            header: '删除审核',
            accept: () => {
                if (item.updateType == '2') {
                    setTimeout(() => {
                        this.deleteWho();//批量删除
                    }, 500);
                } else if (item.updateType == '1') {
                    //单户删除
                    this.deleteFlag.push({
                        appSeqNo: this.deleteParam['appSeqNo'],
                        creditCardNo: this.deleteParam['creditCardNo'],
                        adjustType: '03',
                    });
                    this.commitDelete();
                }
            }
        });
    }

    //选择删除方式
    deleteWho() {
        this.confirmationService.confirm({
            message: '删除单条数据请点确认!删除该批次数据请点取消!',
            header: '删除审核',
            accept: () => {
                this.deleteFlag.push({
                    appSeqNo: this.deleteParam['appSeqNo'],
                    creditCardNo: this.deleteParam['creditCardNo'],
                    adjustType: '03',
                });
                this.commitDelete();
            },
            reject: () => {
                this.deleteFlag.push({
                    appSeqNo: this.deleteParam['appSeqNo'],
                    adjustType: '03'
                });
                this.commitDelete();
            }
        });
    }
    //确认删除
    commitDelete() {
        this.httpService.deleteCeCardAppInfo({ infoList: this.deleteFlag }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: "删除成功!" });
                this.queryFirst();
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        });
    }
    
    queryFirst() {
        this.creditCardQrybean.pageNum = 1;
        this.creditCardQrybean.pageSize = 10;
        this.first = 0;
        this.query();
    }
    //查询
    query() {
        // 日期处理,如果日期为空,则不传给后台
        if (this.startDate != undefined && this.startDate != null) {
            this.creditCardQrybean.startDate = this.commfunc.transDateN(this.startDate);
        } else {
            this.creditCardQrybean.startDate = undefined;
        }
        if (this.endDate != undefined && this.endDate != null) {
            this.creditCardQrybean.endDate = this.commfunc.transDateN(this.endDate);
        } else {
            this.creditCardQrybean.endDate = undefined;
        }
        this.httpService.queryCeCardAssistantUserIdAdjustApply(this.creditCardQrybean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                for (let item of data.infoList) {
                    if ((this.userId == item.agent && item.appStatus == '000')
                        || (this.userId == item.agent && this.userId == item.currTaskActors && (item.appStatus == '991' || item.appStatus == '994'))) {
                        item['this_flag'] = true;
                    } else {
                        item['this_flag'] = false;
                    }
                }
                this.tableValueList = data.infoList;
                this.total = data.totalNum;
                this.isCommitDis = true;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                this.isCommitDis = true;
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            this.isCommitDis = true;
        });
    }
    paginate(event) {
        //每页显示的条数
        this.creditCardQrybean.pageSize = event.rows * 1;
        //当前页
        this.creditCardQrybean.pageNum = event.page + 1;
        this.first = event.page * this.creditCardQrybean.pageSize;
        this.query();
    }
    //重置
    reset() {
        this.agentName = undefined;
        this.handleOrgName = undefined;
        this.startDate = null;
        this.endDate = null;
        this.creditCardQrybean = new CreditCardSavingsAdjustmentQueryBean();
        this.creditCardQrybean.pageNum = 1;
        this.creditCardQrybean.pageSize = 10;
    }
    //回显老协储员信息
    custShow() {
        if (this.creditCardQrybean.creditCardNo && this.creditCardQrybean.creditCardNo != '') {
            let param = {
                creditCardNo: this.creditCardQrybean.creditCardNo
            }
            this.httpService.queryCeCardAssistantUserIdInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.assistantUserIdList = [];
                    for (let item of data.assistantUserId) {
                        this.assistantUserIdList.push({ label: item, value: item })
                    }
                    this.creditCardQrybean.assistantUserId = this.assistantUserIdList[0].value;
                    this.creditCardQrybean.assistantUserName = data.assistantUserName;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }
    //回显新协储员信息
    showName() {
        if (this.creditCardQrybean.newAssistantUserId && this.creditCardQrybean.newAssistantUserId != '') {
            let param = {
                assistantUserId: this.creditCardQrybean.newAssistantUserId,
            }
            this.httpService.queryUserIdOrAssistantUserId(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.creditCardQrybean.newAssistantUserName = data.userName;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            });
        }
    }
    //校验客户号只能输入数字
    onKeyupNumber(event, num) {
        if (event.target.value != '') {
            if (num == 1) {
                if (checkNumber(null, event.target.value)) {
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字!' }];
                    return
                }
            } else if (num == 2) {
                if (checkEnAndNum(null, event.target.value)) {
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字和字母!' }];
                    return
                }
            }
        }
    }
    //提交
    submit(item) {
        this.params = item;
        let a = new Promise((resolve, reject) => {
            this.queryAssistantUserId(resolve);
        })
        //a全部响应成功后执行c
        let c = Promise.all([a]);
        c.then((res) => {
            this.comfirma(res[0]['bussinessMap'])
        })
    }
    comfirma(res_Map) {
        this.confirmationService.confirm({
            message: '确认要提交吗?',
            header: '提交',
            accept: () => {
                //当前为追回打回时
                if (this.params.appStatus == '991' || this.params.appStatus == '994') {
                    this.queryTaskId(this.params);
                } else {
                    let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    if (s.extParam['specifyNextNode']) {
                        delete s.extParam['specifyNextNode']
                        let commonHeader = s;
                        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                    }
                    if (s.extParam.specifyNextOpers) {
                        delete s.extParam.specifyNextOpers
                        let commonHeader = s;
                        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                    }
                    this.waitSelUserList = this.commfunc.query('XYK_XCYHTZ', res_Map);
                    if (!this.waitSelUserList.erro) {
                        this.outVal = this.waitSelUserList.waitSelUserList;
                        let chooseThis = this.waitSelUserList.display;
                        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                        let a2 = commonHeader.extParam.specifyNextOpers;
                        if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
                            this.downloadPerson = true;
                        } else {
                            if (this.params.updateType == '1') {
                                this.commit();
                            } else if (this.params.updateType == '2') {
                                this.batchSub();
                            }
                        }
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
                    }
                }
            }
        });
    }
    //单户提交
    commit() {
        this.isCommitDis = false;
        this.params['operationType'] = '2';
        this.httpService.updateCeCardAssistantUserIdAdjustApply(this.params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '提交成功!' }];
                setTimeout(() => {
                    this.queryFirst();
                }, 1000)
            } else {
                this.isCommitDis = true;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.isCommitDis = true;
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    outDisplayCall(event) {
        this.downloadPerson = event;
    }
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        let specifyNextNode = '';//节点处理名称
        this.outVal.forEach(item => {
            ss += item.tellerId + ',';
            specifyNextNode = item.outFlowId;
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        commonHeader.extParam['specifyNextNode'] = specifyNextNode;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        if (this.params.updateType == '1') {
            this.commit();
        } else if (this.params.updateType == '2') {
            this.batchSub();
        }
    }
    downloadHide() {
        this.downloadPerson = false;
    }
    //批量提交
    batchSub() {
        this.isCommitDis = false;
        this.params['successNum'] = '1';
        this.params['adjustType'] = '03';
        this.httpService.submitBatchApply(this.params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs = [{ severity: 'success', summary: '提示', detail: '提交成功!' }];
                setTimeout(() => {
                    this.queryFirst();
                }, 1000)
            } else {
                this.isCommitDis = true;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.isCommitDis = true;
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
        });
    }
    cancelCall(event) {
        if (event) {
            this.valueAppSeqNo = event;
            if (event.flag == 1 || event.flag == 2) {
                this.display = false;
            }
        }
    }
    //弹出框取消
    onClose() {
        //新增
        if (this.dispalyShow == '1'
            && this.valueAppSeqNo
            && (this.valueAppSeqNo.flag == 0 || this.valueAppSeqNo.flag == 1)) {
            let arr = [];
            arr.push({
                appSeqNo: this.valueAppSeqNo['appSeqNo'],
                adjustType: this.valueAppSeqNo['adjustType'],
            });
            let param = {
                infoList: arr
            };
            this.httpService.deleteCeCardAppInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                }
            });
        }
    }
    //追回打回的选人
    agree(param_) {
        this.bussinessMap.approve = '0';
        this.bussinessMap['dataMap'] = param_;
        this.subApproveMapBean.result = '0';
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        let param = {
            userIdMap: { userId: this.userId, userPost: this.extParam.postId },
            proxessDesc: '选人策略',
            taskId: param_.taskId,
            // taskId: '2975258',
            bussinessMap: this.bussinessMap,
            approveMap: this.subApproveMapBean,
        }
        this.customeHttpService.bpmFindNextNodeInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.result.nextOutData && data.result.nextOutData.length >= 1) {
                    let waitSelectUserList = data.result.nextOutData;
                    this.extableData = [];
                    this.extableData = waitSelectUserList;
                    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    let a = commonHeader.extParam.specifyNextOpers;
                    if (a == undefined || a == '' || a == null) {
                        if (this.extableData && this.extableData.length >= 1) {
                            if (this.extableData.length == 1 &&
                                ((this.extableData[0].selectUserList && this.extableData[0].selectUserList.length == 0) ||
                                    (this.extableData[0].selectUserList == undefined))) {
                                this.examineDisplay = false;
                                this.disposeSchedule(param_);
                            } else {
                                if (this.extableData.length == 1) {
                                    this.checkValueNode = this.extableData[0].outFlowId;
                                    if (this.extableData[0].nodeType == 'sign') {
                                        this.mm = [];
                                        if (this.extableData[0].selectUserList && this.extableData[0].selectUserList.length > 0) {
                                            for (let i = 0; i < this.extableData[0].selectUserList.length; i++) {
                                                this.mm.push(this.extableData[0].selectUserList[i].userId)
                                            }
                                        }
                                        this.checkValuePeopleSign = _.clone(this.mm);
                                    } else {
                                        this.checkValuePeopleSign = [];
                                    }
                                }
                                this.examineDisplay = true;
                            }
                        }

                    } else {
                        this.disposeSchedule(param_);
                    }
                } else {
                    this.disposeSchedule(param_);
                }
            }
            else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
    }
    disposeSchedule(param_) {
        this.bussinessMap.specifyNextNode = this.checkValueNode;
        if (this.checkValuePeople) {
            let sIndex = '';
            this.extableData.forEach((element, index) => {
                if (this.checkValueNode == element.outFlowId) {
                    sIndex = index + '';
                }
            });
            if (sIndex.length > 0) {
                let num = this.checkValuePeople.length - sIndex.length;
                this.bussinessMap.specifyNextOpers = this.checkValuePeople.substring(0, num) + ',';
            } else {
                this.bussinessMap.specifyNextOpers = this.checkValuePeople + ',';
            }
        }
        this.appproResultBean.bussinessMap = this.bussinessMap;
        this.appproResultBean.userId = this.userId;
        this.appproResultBean.approveMap = this.subApproveMapBean;
        this.appproResultBean.taskId = param_.taskId;
        // this.appproResultBean.taskId = '2975258';
        this.customeHttpService.bpmNextTask(this.appproResultBean).subscribe(data => {
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (data.returnCode.code == 'success') {
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.msgs = [{ severity: 'success', summary: '提示', detail: '提交成功!' }];
                setTimeout(() => {
                    this.queryFirst();
                }, 1000)
            } else {
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (s.extParam.specifyNextOpers) {
                delete s.extParam.specifyNextOpers
                let commonHeader = s;
                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
            }
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }
    doSubmit() {

        if (this.checkValueNode == '' || this.checkValueNode == undefined) {
            this.msgs = [];
            this.msgs.push({ severity: "error", summary: "提示", detail: "请选择下一处理节点" })
            return
        }

        let ss = '';
        // 获取选中节点类型
        this.extableData.forEach((element, index) => {
            if (this.checkValueNode == element.outFlowId) {
                ss = element.nodeType
            }
        });

        if (this.checkValuePeople == '' || this.checkValuePeople == undefined) {
            // 如果是会签节点和自动节点
            if (ss == 'sign' || ss == 'auto' || ss == 'end') {

            } else {
                this.msgs = [];
                this.msgs.push({ severity: "error", summary: "提示", detail: "请选择下一节点处理人" })
                return
            }
        }
        this.examineDisplay = false;
        this.disposeSchedule(this.params);
    }
    choosePeople(col, ri) {
        // 清空会签信息
        this.checkValuePeopleSign = [];
        // 选中当前节点
        this.checkValueNode = col.outFlowId;
    }
    // 选择节点
    chooseNode(param, rowIndexNode) {
        if (param.outFlowId != this.checkValueNode) {
            this.checkValuePeople = undefined;
        }

        if (param.nodeType == 'sign') {
            this.mm = [];
            if (param.selectUserList && param.selectUserList.length > 0) {
                for (let i = 0; i < param.selectUserList.length; i++) {
                    this.mm.push(param.selectUserList[i].userId)
                }
            }
            this.checkValuePeopleSign = _.clone(this.mm);
        } else {
            this.checkValuePeopleSign = [];
        }
        this.chosseNodeIndex = rowIndexNode;
    }
    //追回打回的关闭弹窗
    closeBack() {
        this.examineDisplay = false;
        this.extableData = [];
        this.checkValueNode = undefined;
        this.checkValuePeople = undefined;
    }
    //查询打回追回状态的taskid
    queryTaskId(items) {
        let paramItem = {
            processId: items.standby2
        }
        this.httpService.bpmQueryUnDoTaskList(paramItem).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.pageData.result && data.pageData.result.length > 0) {
                    items['taskId'] = data.pageData.result[0].taskId;
                    this.agree(items);
                }
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }
    //查询原协储员与新协储员同属哪个部门
    queryAssistantUserId(resolve) {
        let param = {
            oldUserId: this.params.assistantUserId,
            newUserId: this.params.newAssistantUserId,
            oldOrgId: this.params.assistantUserIdDot,
            newOrgId: this.params.newAssistantUserIdDot,
        }
        this.httpService.queryAssistantUserIdAscription(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.bussinessMap = _.clone(data.resultMap);
                // this.bussinessMap['ascription'] = data.resultMap.ascription;
                // this.bussinessMap['adjustUserId'] = data.resultMap.adjustUserId;
                // this.bussinessMap['receiveUserId'] = data.resultMap.receiveUserId;
                // this.bussinessMap['userId'] = data.resultMap.userId;
                this.bussinessMap['userPost'] = this.extParam.postId;
                this.bussinessMap['userIdMap'] = {
                    userId: this.userId,
                    userPost: this.extParam.postId
                }
                resolve({ bussinessMap: this.bussinessMap });
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }

    //选择员工
    chooseWorker() {
        this.userInValue = '0000';
        this.personPage = !this.personPage;
    }
    //关闭模态框
    onHideClose() {
        this.personPage = false;
        this.orgTreeDisplay = false;
    }
    //员工回调
    perInfor(item) {
        this.personPage = false;
        if (item.length > 0) {
            this.creditCardQrybean.agent = item[0].tellerId;
            this.agentName = item[0].tellerName;
        }
    }
    //选择机构
    openOrg(param) {
        this.orgTreeDisplay = !this.orgTreeDisplay;
        this.ownerOrgInValue = 'adjustOrg'
    }
    //机构树回调
    treeCall(param) {
        this.orgTreeDisplay = false;
        this.handleOrgName = param.orgName;
        this.creditCardQrybean.handleOrg = param.orgId;
    }

    // 开始、结束日期判断
    showTime(param1, param2) {
        if (param1 == 'plan') {
            if (this.startDate && this.endDate) {
                if (this.startDate > this.endDate) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                    setTimeout(() => {
                        if (param2 == "start") {
                            this.startDate = null;
                        } else {
                            this.endDate = null;
                        }
                    })
                }
            }
        }
    }

    // 手动输入日期
    handInputDate(param1, param2) {
        this.showTime(param1, param2);
    }

    // 导出
    doExport() {
        let InParam = {};
        let InParamIn = {};
        InParam = _.clone(this.creditCardQrybean);
        if (InParam['commonHeader']) {
            delete InParam['commonHeader']
        }
        // 日期处理,如果日期为空,则不传给后台
        if (this.startDate != undefined && this.startDate != null) {
            InParam['startDate'] = this.commfunc.transDateN(this.startDate);
        } else {
            InParam['startDate'] = undefined;
        }
        if (this.endDate != undefined && this.endDate != null) {
            InParam['endDate'] = this.commfunc.transDateN(this.endDate);
        } else {
            InParam['endDate'] = undefined;
        }
        InParamIn['condition'] = InParam;
        InParamIn['adjustType'] = '03';
        this.cusOperationHttpService.cecardAdjustExport(InParamIn).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let aa = data.fileName;
                window.open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(aa) + "&fileUrl=" + data.fileUrl)
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
    }

}